<template>
    <PlekollectionList 
        v-if="this.$store.state.userData.username"
    />
    
    <div v-if="Object.keys(this.$store.state.userEmail).length !== 0&&!this.$store.state.userData.username">
        <LoadUserProfile/>
    </div>
</template>

<script>
import PlekollectionList from "@/components/PlekollectionList";
import LoadUserProfile from "@/components/LoadUserProfile";

export default {
    components:{
        PlekollectionList,
        LoadUserProfile
    },
    methods:{
        
    },
    beforeCreate() {
		if (Object.keys(this.$store.state.userEmail).length === 0) {
			this.$store.dispatch("auth0getUserEmail");
		}
	}
}
</script>

