<template>
	<div id="mapContainer"></div>
</template>

<script>
import L from "leaflet";
import "leaflet-draw";

// fix default icon
delete L.Icon.Default.prototype._getIconUrl  
L.Icon.Default.mergeOptions({  
  iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),  
  iconUrl: require('leaflet/dist/images/marker-icon.png'),  
  shadowUrl: require('leaflet/dist/images/marker-shadow.png')  
})

export default {
	props: ["featCols"],
	data() {
		return {
			center: [51.11, 4.46],
			markerIcon: L.icon({
				iconUrl: require("../assets/logo.png"),
				iconSize: [34, 34],
			}),
			osm_layer: L.tileLayer(
				"https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
			),
			layercontrol: L.control.layers(),
			polygonMarkerOptions: {
				radius: 2,
				fillColor: "green",
				color: "green",
				weight: 2,
				opacity: 0.5,
				fillOpacity: 0.2,
			},
			pointMarkerOptions: {
				radius: 3,
				fillColor: "red",
				color: "red",
				weight: 3,
				opacity: 1,
				fillOpacity: 0.2,
			},
		};
	},
	methods: {
		setupLeafletMap() {
			// init map
			this.map = L.map("mapContainer", {
				center: this.center,
				crs: L.CRS.EPSG3857,
				zoom: 11,
				zoomControl: true,
				preferCanvas: false,
			});

			// add osm tile layer
			this.osm_layer.addTo(this.map);

			// add scale
			L.control.scale().addTo(this.map);

			// add layer control
			this.layercontrol.addTo(this.map);
			this.layercontrol.addBaseLayer(this.osm_layer, "OSM layer");
		},
        doPrompt(question) {
            return prompt(question, " ");
        },
		addDrawControl() {
            let drawnItems = L.featureGroup().addTo(this.map);
			this.layercontrol.addOverlay(drawnItems, "drawn items");

			this.map.addControl(
				new L.Control.Draw({
					edit: {
						featureGroup: drawnItems,
						poly: {
							allowIntersection: false,
						},
					},
					draw: {
						polygon: true, // {allowIntersection: false,showArea: true,},
						polyline: true,
						rectangle: false,
						circlemarker: false,
						circle: false,
						marker: true,
					},
				})
			);

			this.map.on(L.Draw.Event.CREATED, function (event) {
				var layer = event.layer;

				//if(event.layerType=="marker"){}

				layer.properties = {};
				var feature = (layer.feature = layer.feature || {});
				feature.type = "Feature";
				feature.properties = feature.properties || {};

			    let marker_name = prompt("enter location name", " ");

				if (marker_name != null && marker_name != " ") {
				 	feature.properties["name"] = marker_name;

					layer.bindTooltip(feature.properties["name"]);

				 	drawnItems.addLayer(layer);
				}
			});
		},
		addToolTip(feature, layer) {
			if (feature.properties && feature.properties.name) {
				layer.bindTooltip(feature.properties.name);
			}
		},
		displayFeatureCollectionsOnMap() {
			this.featCols.forEach((featCol) => {
				let layerNames = this.layercontrol._layers.map((e) => e.name);
				if (!layerNames.includes(featCol.name)) {
					let new_geojson_feature = L.featureGroup().addTo(this.map);
					featCol.features.forEach((f) => {
						switch (f.geometry.type) {
							case "Point":
								let icon = this.markerIcon;
								L.geoJSON(f, {
									pointToLayer: function (feature, latlng) {
										//return L.marker(latlng, { icon: icon });
										return L.circleMarker(
											latlng,
											this.pointMarkerOptions
										);
									},
									onEachFeature: this.addToolTip,
								}).addTo(new_geojson_feature);
								break;
							case "Polygon":
							case "MultiPolygon":
								L.geoJSON(f, {
									style: this.polygonMarkerOptions,
									onEachFeature: this.addToolTip,
								}).addTo(new_geojson_feature);
								break;
							default:
								L.geoJSON(f, {
									onEachFeature: this.addToolTip,
								}).addTo(new_geojson_feature);
						}
					});

					this.layercontrol.addOverlay(
						new_geojson_feature,
						featCol.name
					);
				}
			});
		},
	},
	watch: {
		featCols: {
			handler(n, o) {
				this.displayFeatureCollectionsOnMap();
			},
			deep: true,
		},
	},
	mounted() {
		this.setupLeafletMap();
        if(this.$store.state.userIsAuthenticated){
            this.addDrawControl();
        }
	},
	beforeUnmount() {
		if (this.map) {
			this.map.remove();
		}
	},
};
</script>

<style scoped>
#mapContainer {
	width: 100vw;
	height: calc(100% - 48px);
	position: absolute;
	top: 48px;
}
#menubutton {
	margin-top: 10px;
	margin-left: 50px;
	position: relative;
	z-index: 9999;
	pointer-events: auto;
}
</style>